import React, { useState, useEffect } from 'react';
import { Box, CssBaseline, ThemeProvider, createTheme, Typography, useMediaQuery, Fab, Tooltip, Container } from '@mui/material';
import './App.css';
import Sidebar from './components/Sidebar';
import AnimeList from './components/AnimeList';
import { getAnimeData, getSeasons } from './services/api';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import UnfoldLessIcon from '@mui/icons-material/UnfoldLess';

// 
const theme = createTheme({
  palette: {
    primary: { main: '#2563eb', light: '#60a5fa', dark: '#1e40af' },
    secondary: { main: '#dc004e' },
    background: { default: '#f7f9fc' }
  },
  shape: { borderRadius: 12 },
  typography: {
    fontFamily: `'Inter', 'Noto Sans SC', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, 'Apple Color Emoji', 'Segoe UI Emoji'`,
    h5: { fontWeight: 700 },
    h6: { fontWeight: 700 },
  },
  components: {
    MuiCard: {
      styleOverrides: {
        root: {
          transition: 'box-shadow .2s ease, transform .2s ease',
          '&:hover': {
            boxShadow: 4,
            transform: 'translateY(-1px)'
          },
        },
      },
    },
    MuiAccordion: {
      styleOverrides: {
        root: {
          borderRadius: 10,
          '&:before': { display: 'none' },
        },
      },
    },
  },
});

function App() {
  const [selectedSeason, setSelectedSeason] = useState(null);
  const [animeData, setAnimeData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [collapseSignal, setCollapseSignal] = useState(0);

  // 
  useEffect(() => {
    const initSeason = async () => {
      try {
        const seasons = await getSeasons();
        if (seasons && seasons.length > 0) {
          setSelectedSeason(seasons[0]);
        }
      } catch (e) {
        console.error('', e);
      }
    };
    initSeason();
  }, []);

  // 
  useEffect(() => {
    if (selectedSeason) {
      fetchAnimeData(selectedSeason);
    }
  }, [selectedSeason]);

  // 
  const fetchAnimeData = async (season) => {
    setLoading(true);
    try {
      // API
      const data = await getAnimeData(season.year, season.month);
      console.log(':', data); // 
      setAnimeData(data);
    } catch (error) {
      console.error(':', error);
      // 
      setAnimeData([]);
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleSeasonSelect = (season) => {
    setSelectedSeason(season);
  };

  const handleScrollTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const handleCollapseAll = () => {
    setCollapseSignal((prev) => prev + 1);
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Box sx={{ display: 'flex', height: '100vh', flexDirection: { xs: 'column', md: 'row' } }}>
        <Sidebar onSeasonSelect={handleSeasonSelect} selectedSeason={selectedSeason} />
        <Box component="main" sx={{ 
          flexGrow: 1, 
          p: 3, 
          mt: { xs: 5, md: 0 },
          width: { xs: '100%', md: 'auto' }
        }}>
          <Container maxWidth={false} disableGutters>
            {selectedSeason && (
              <Box sx={{ mb: 2 }}>
                <Typography variant="h5" color="primary" sx={{ letterSpacing: 0.3 }}>
                  {selectedSeason.year} {getSeasonName(selectedSeason.month)}
                </Typography>
              </Box>
            )}
            <AnimeList animeData={animeData} loading={loading} collapseSignal={collapseSignal} />
          </Container>

          {/* Floating action buttons at bottom-right */}
          <Box sx={{ position: 'fixed', right: 16, bottom: 16, display: 'flex', flexDirection: 'column', gap: 1, zIndex: (theme) => theme.zIndex.modal + 1 }}>
            <Tooltip title="" placement="left" arrow>
              <Fab color="primary" size="medium" aria-label="top" onClick={handleScrollTop}>
                <KeyboardArrowUpIcon />
              </Fab>
            </Tooltip>
            <Tooltip title="" placement="left" arrow>
              <Fab color="primary" size="medium" aria-label="collapse all" onClick={handleCollapseAll}>
                <UnfoldLessIcon />
              </Fab>
            </Tooltip>
          </Box>
        </Box>
      </Box>
    </ThemeProvider>
  );
}

// 
const getSeasonName = (month) => {
  switch (month) {
    case 1: return '';
    case 4: return '';
    case 7: return '';
    case 10: return '';
    default: return '';
  }
};

export default App;
